body {
  color: #ebdbb2;
  background-color: #282828;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
}

h1 {
  text-align: center;
  color: #fabd2f;
}

.drag-drop {
  text-align: center;
  cursor: pointer;
  background-color: #3c3836;
  border: 2px dashed #928374;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 200px;
  padding: 20px;
  display: flex;
}

.drag-drop-text {
  color: #928374;
  text-align: center;
  margin-bottom: 20px;
  font-size: 16px;
}

.file-input-label {
  display: none;
}

.file-input-label + .file-input-display {
  color: #ebdbb2;
  margin-top: 10px;
  font-size: 14px;
}

.file-input-label:hover {
  text-decoration: underline;
}

.file-input-label input[type="file"] {
  display: none;
}

.drag-drop p {
  color: #928374;
}

.drag-drop input[type="file"] {
  display: none;
}

.drag-drop:hover {
  border-color: #fabd2f;
}

.form {
  margin-top: 20px;
}

label {
  color: #fe8019;
  margin-bottom: 5px;
  display: block;
}

input[type="number"] {
  color: #ebdbb2;
  background-color: #3c3836;
  border: none;
  border-radius: 4px;
  width: 80px;
  padding: 5px;
}

input[type="number"]:focus {
  outline: none;
  box-shadow: 0 0 3px #fabd2f;
}

button {
  color: #282828;
  cursor: pointer;
  background-color: #fabd2f;
  border: none;
  border-radius: 4px;
  margin-top: 10px;
  padding: 10px 15px;
  transition: background-color .3s;
}

button:hover {
  background-color: #d79921;
}

